import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'

Vue.use(Vuex)

const store = new Vuex.Store({
  strict: (process.env.MODE_ENV !== 'production'),
  modules: {
    // be aware of that modules are namespaced
    user
  }
})

export default store



