<template>
  <div>
    <h1>&lt;Company/Home_Template&gt;</h1>
    <h3>Received Data:</h3>
    <div v-for="(v, k) in info" :key="k" style="margin: 7px;">
      <strong>{{ k }}</strong>:
      <p style="margin: 0; overflow: auto;" >{{ v }}</p>
    </div>
    <h3>Treefy:</h3>
    <dept-node :node="info.depts"/>
    <div v-for="(board, index) in info.boards" :key="index">
      <button @click="bringPosts(board.id)">Open board {{ board.id }} - {{ board.name }}</button>
    </div>
    <p v-for="(post, index) in posts" :key="index">{{ post }}</p>
  </div>
</template>

<script>
import Vue from 'vue'

// make it single file component later
Vue.component('dept-node', {
  template: `
  <div style="margin-left: 10px;">
    <template v-if="node">
      id: {{ node.id || "null" }}
      name: {{ node.name }}
      <dept-node v-for="(child, index) in node.children" :key="index" :node="child"/>
    </template>
  </div>`,
  props: {
    node: Object
  }
})

export default {
  data() {
    return {
      info: {
        name  : null,
        logo  : null,
        depts : null,
        boards: null
      },
      posts: []
    }
  },
  methods: {
    bringPosts(boardID) {
      this.$axios.get(`/api/board/${boardID}`, { params: { start: 0, len: 100 } })
      .then(results => {
        this.posts = results.data
      })
      .catch(err => {})
    }
  },
  created() {
    this.$axios.get('/api/company')
    .then(({ data }) => {
      // process raw hierarchical data into tree
      let root = {
        id: null,
        name: 'Root',
        children: []
      }
      data.depts.push(root)
      data.depts.forEach(dept => {
        dept.children = data.depts.filter(child => child.parentID === dept.id) || []
      })
      // assign it
      data.depts = root
      Object.zip(this.info, data)
    })
    .catch(err => {})
  }
}
</script>

<style scoped>

</style>



